import React from 'react';
import {View, StyleSheet} from 'react-native';
import {useTheme} from 'react-native-paper';
import {useDispatch, useSelector} from 'react-redux';
import {IApplicationState} from '../redux/appReducer';
import {repoActions} from '../redux/repo/actions';
import {Button} from 'react-native-paper';

const Pagination = () => {
  const dispatch = useDispatch();
  const theme = useTheme();
  const {page, hasMoreIssues, isLoading} = useSelector(
    (state: IApplicationState) => state.repo,
  );
  const isPreviousDisabled = page === 1;
  const isNextDisabled = hasMoreIssues === false;
  return (
    <View style={[styles.row, {margin: theme.spacing.l}]}>
      <Button
        disabled={isLoading || isPreviousDisabled}
        onPress={() => dispatch(repoActions.getRepoIssues({page: page - 1}))}
        mode="contained">
        Previous
      </Button>
      <Button
        disabled={isLoading || isNextDisabled}
        loading={isLoading}
        onPress={() => dispatch(repoActions.getRepoIssues({page: page + 1}))}
        mode="contained">
        Next
      </Button>
    </View>
  );
};

export default Pagination;

const styles = StyleSheet.create({
  container: {
    // alignItems: 'center',
  },
  row: {
    flexDirection: 'row',
    justifyContent: 'space-between',
  },
});
